fault;\n$grid-xs-min: px-rem(480) !default;\n$grid-sm-min: px-rem(768) !default;\n$grid-md-min: px-rem(1024) !default;\n$grid-lg-min: px-rem(1200) !default;\n\n$half-gutter-width: $grid-gutter-width * 0.5;\n$gutter-compensation: $half-gutter-width * -1;\n$container-sm: $grid-sm-min + $grid-gutter-width;\n$container-md: $grid-md-min + $grid-gutter-width;\n$container-lg: $grid-lg-min + $grid-gutter-width;\n\n.container-fluid,\n.container {\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.container {\n  padding-left: $grid-gutter-width / 2;\n  padding-right: $grid-gutter-width / 2;\n}\n\n.container-fluid {\n  padding-left: $grid-outer-margin;\n  padding-right: $grid-outer-margin;\n}\n\n.row {\n  box-sizing: border-box;\n  display: flex;\n  flex: 0 1 auto;\n  flex-direction: row;\n  flex-wrap: wrap;\n  margin-left: $gutter-compensation;\n  margin-right: $gutter-compensation;\n\n  &.reverse {\n    flex-direction: row-reverse;\n  }\n}\n\n.col {\n  box-sizing: border-box;\n  flex: 0 0 auto;\n  padding-left: $half-gutter-width;\n  padding-right: $half-gutter-width;\n\n  &.reverse {\n    flex-direction: column-reverse;\n  }\n}\n\n@mixin create-cols($v: \"xs\") {\n  .col-#{$v} {\n    flex-basis: 0;\n    flex-grow: 1;\n    max-width: 100%;\n  }\n\n  @for $col-index from 1 through $grid-cols {\n    $col-width: ((100 / $grid-cols) * $col-index) * 1%;\n\n    .col-#{$v}-#{$col-index} {\n      flex-basis: $col-width;\n      max-width: $col-width;\n    }\n\n    .col-#{$v}-offset-#{$col-index} {\n      margin-left: $col-width;\n    }\n  }\n\n  .start-#{$v} {\n    justify-content: flex-start;\n  }\n\n  .center-#{$v} {\n    justify-content: center;\n  }\n\n  .end-#{$v} {\n    justify-content: flex-end;\n  }\n\n  .top-#{$v} {\n    align-items: flex-start;\n  }\n\n  .middle-#{$v} {\n    align-items: center;\n  }\n\n  .bottom-#{$v} {\n    align-items: flex-end;\n  }\n\n  .around-#{$v} {\n    justify-content: space-around;\n  }\n\n  .between-#{$v} {\n    justify-content: space-between;\n  }\n\n  .first-#{$v} {\n    order: -1;\n  }\n\n  .last-#{$v} {\n    order: 1;\n  }\n}\n\n@include create-cols(\"xs\");\n\n@include sm-viewport {\n  @include create-cols(\"sm\");\n  .container {\n    width: $container-sm;\n  }\n}\n\n@include md-viewport {\n  @include create-cols(\"md\");\n  .container {\n    width: $container-md;\n  }\n}\n\n@include lg-viewport {\n  @include create-cols(\"lg\");\n  .container {\n    width: $container-lg;\n  }\n}\n","@function strip-units($number) {\n  @return $number / ($number * 0 + 1);\n}\n\n@function px-rem($px, $base-px: 16) {\n  @return (strip-units($px) / strip-units($base-px)) * 1rem;\n}\n\n@function px-em($px, $base-px: 16) {\n  @return (strip-units($px) / strip-units($base-px)) * 1em;\n}\n\n// Math\n@function pow($number, $exponent) {\n  $value: 1;\n\n  @if $exponent > 0 {\n    @for $i from 1 through $exponent {\n      $value: $value * $number;\n    }\n  }\n\n  @return $value;\n}\n","// Try to use these simple mixins if possible for your responsive needs\n\n// Example usage:\n//\n// .grid {\n//  display: flex;\n//  flex-direction: column;\n//  @include md-viewport {\n//    flex-direction: row;\n//  }\n// }\n\n@mixin sm-viewport {\n  @media only screen and (min-width: $grid-sm-min) {\n    @content;\n  }\n}\n\n@mixin md-viewport {\n  @media only screen and (min-width: $grid-md-min) {\n    @content;\n  }\n}\n\n@mixin lg-viewport {\n  @media only screen and (min-width: $grid-lg-min) {\n    @content;\n  }\n}\n\n// Media query abstractions if you need more complex responsive behavior\n// or custom breakpoints\n\n@mixin screen-size-lte($max) {\n  @media screen and (max-width: $max) {\n    @content;\n  }\n}\n\n@mixin screen-size-between($min, $max) {\n  @media screen and (min-width: $min) and (max-width: $max) {\n    @content;\n  }\n}\n\n@mixin screen-size-gte($min) {\n  @media screen and (min-width: $min) {\n    @content;\n  }\n}\n"]}]);

