;\n    }\n  }\n\n  &:hover {\n    box-shadow: 0 2px 4px 0 $btn-box-shadow-color;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color;\n  }\n\n  &:disabled {\n    border-color: $btn-disabled-color;\n    color: $btn-disabled-color;\n    background-color: transparent;\n  }\n}\n\n@mixin button-flat-variant($color) {\n  color: $color;\n  background-color: transparent;\n\n  &:hover,\n  &:active {\n    box-shadow: none;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color;\n  }\n\n  &:disabled {\n    color: $btn-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Button sizes\n@mixin button-size(\n  $padding-y,\n  $padding-x,\n  $font-size,\n  $line-height,\n  $min-width\n) {\n  padding: $padding-y $padding-x;\n  font-size: $font-size;\n  line-height: $line-height;\n  min-width: $min-width;\n\n  &.fit-text {\n    min-width: 0;\n    min-height: 0;\n  }\n}\n\n@mixin button-variants($name, $color, $background, $border: transparent) {\n  .btn-#{$name} {\n    @include button-variant($color, $background, $border);\n    &.outline {\n      @include button-outline-variant($background);\n    }\n    &.flat {\n      @include button-flat-variant($background);\n    }\n    @content;\n  }\n  .link-#{$name} {\n    font-weight: bold;\n    @include font-smoothing;\n    color: $background;\n    text-decoration: underline;\n\n    &:disabled {\n      color: $btn-disabled-color;\n    }\n  }\n}\n\n// Shamelessly copied from https://codepen.io/davidhalford/pen/wlDxL\n// Adds text color to an element based on what has more contrast with its background\n\n@function color-brightness($color) {\n  @return round(\n    (red($color) * 299) + (green($color) * 587) + (blue($color) * 114) / 1000\n  );\n}\n\n$brightness-threshold: color-brightness($color-white) / 2;\n\n@function text-contrast-color($background) {\n  $brightness: color-brightness($background);\n\n  @if abs($brightness) < $brightness-threshold {\n    @return #fff;\n  }\n\n  @return #000;\n}\n","@function strip-units($number) {\n  @return $number / ($number * 0 + 1);\n}\n\n@function px-rem($px, $base-px: 16) {\n  @return (strip-units($px) / strip-units($base-px)) * 1rem;\n}\n\n@function px-em($px, $base-px: 16) {\n  @return (strip-units($px) / strip-units($base-px)) * 1em;\n}\n\n// Math\n@function pow($number, $exponent) {\n  $value: 1;\n\n  @if $exponent > 0 {\n    @for $i from 1 through $exponent {\n      $value: $value * $number;\n    }\n  }\n\n  @return $value;\n}\n","// NOTE: When updating, make sure you also update the gamut-styles/variables/index.js file\n\n// =======================================\n//      COLORS\n// =======================================\n\n$color-blue-100: #c8d7fa;\n$color-blue-200: #a5befa;\n$color-blue-300: #7da2fa;\n$color-blue-400: #5788fa;\n$color-blue-500: #3069f0;\n$color-blue-600: #2d5dcc;\n$color-blue-700: #2e4a99;\n$color-blue-800: #233466;\n$color-blue-900: #141e3b;\n$color-blue-1000: #10162f;\n$color-blue-1100: #0a0e1d;\n\n$color-pink-100: #ffd9fc;\n$color-pink-200: #ffbffa;\n$color-pink-300: #ffa6f8;\n$color-pink-400: #f288e9;\n$color-pink-500: #d957d9;\n$color-pink-600: #b035c9;\n$color-pink-700: #9129a6;\n$color-pink-800: #702080;\n$color-pink-900: #43134d;\n\n$color-purple-100: #d5ccff;\n$color-purple-200: #c0b6f2;\n$color-purple-300: #ac9df2;\n$color-purple-400: #917ef2;\n$color-purple-500: #7c5ce6;\n$color-purple-600: #6437cc;\n$color-purple-700: #4b2999;\n$color-purple-800: #381f73;\n$color-purple-900: #231347;\n\n$color-red-100: #ffd3cc;\n$color-red-200: #ffb8ad;\n$color-red-300: #ff988c;\n$color-red-400: #ff7566;\n$color-red-500: #fd4d3f;\n$color-red-600: #e53935;\n$color-red-700: #bf2e2c;\n$color-red-800: #992523;\n$color-red-900: #661917;\n\n$color-green-100: #bbfae5;\n$color-green-200: #91f2d2;\n$color-green-300: #6aebc0;\n$color-green-400: #4fe0b0;\n$color-green-500: #47cca0;\n$color-green-600: #3eb38c;\n$color-green-700: #318c6e;\n$color-green-800: #246650;\n$color-green-900: #164032;\n\n